import PropTypes from 'prop-types';
import React     from 'react';

const IconProfitLoss = ({ type }) => {
    let IconType;
    if (type) {
        switch (type) {
            case 'profit':
                IconType = (
                    <g fill='none' fillRule='evenodd'>
                        <path fill='#2BC8A5' fillRule='nonzero' d='M8 4l6 8H2z' />
                    </g>
                );
                break;
            case 'loss':
                IconType = (
                    <g fill='none' fillRule='evenodd'>
                        <path fill='#E31C4B' fillRule='nonzero' d='M8 12l6-8H2z' />
                    </g>
                );
                break;
            default:
                IconType = (
                    <path fill='#E31C4B' fillRule='nonzero' d='M8 12l6-8H2z' />
                );
                break;
        }
    }
    return (
        <svg className='inline-icon' width='16' height='16' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'>
            {IconType}
        </svg>

    );
};

IconProfitLoss.propTypes = {
    type: PropTypes.string,
};

export { IconProfitLoss };
